@import "../../styles/variables";

.Products {
  background-color: #fff;
  //background-image: url("../../assets/images/home/home_footer.svg");
  background-repeat: no-repeat;
  background-position: bottom center;
  background-size: contain;
  font-size: 1rem;
  position: relative;
  width: 100%;
  margin-top: -70px;
  .container {
    @media (min-width: 576px) {
      max-width: 560px;
    }
    @media (min-width: 768px) {
      max-width: 640px;
    }
    @media (min-width: 992px) {
      max-width: 880px;
    }
    @media (min-width: 1200px) {
      max-width: 1260px;
    }
  }
}

/* Download Products section*/

.products-header-block h1 {
  font-family: "Open sans", sans-serif !important;
  font-style: normal;
  font-weight: 600;
  margin: 3% 10%;
}

.products-container {
  display: flex;
  justify-content: space-around !important;
  flex-direction: row;
  margin-bottom: 10%;
}

.product-block {
  padding: 0 !important;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.product-block-description {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 0 !important;
}

.product-description {
  font-family: "Open sans", sans-serif !important;
  padding-top: 0 !important;
  font-style: normal;
  font-weight: normal;
  font-size: 2rem;
  line-height: 2.3rem;
}

.product-number {
  margin-right: 20px;
}

.product-number-img {
  width: 70% !important;
  margin-right: 40px;
}

.product-block-download {
  text-align: center;
  margin-top: 40px;
}

.product-download-button {
  background-color: #34d086 !important;
  box-shadow: 0px 4px 5px rgba(0, 0, 0, 0.1);
  border-radius: 15px;
  color: #142e50 !important;
  padding: 15px 70px;
  border: 0 !important;
  font-weight: 400;
  font-size: 1.5rem;
  text-align: center;
}

/* Devices image Section */

.devices {
  display: flex;
  justify-content: center;
  margin: 0 0 10%;
}

.devices img {
  width: 75%;
}

/* Footer Section */

.get-started-footer {
  background: url("../../assets/images/home/home_footer.svg") bottom/cover no-repeat;
  height: 20vw;
  margin-top: -10%;
}

.footer-links {
  padding-top: 7%;
  padding-left: 8%;
}

.footer-link {
  font-family: "Open sans", sans-serif !important;
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 30px;
}

.footer-mail {
  font-family: "Open sans", sans-serif !important;
  color: #fff !important;
  text-decoration: underline;
  font-size: 18px;
  padding-top: 2%;
  padding-left: 60%;
}

@media (max-width: 768px) {
  .products-container {
    padding: 10px;
  }
  .get-started-footer {
    height: 20vh;
    margin-top: -8%;
  }
  .footer-mail {
    padding-left: 50%;
  }
}

@media (max-width: 610px) {
  .products-container {
    flex-direction: column;
  }
  .product-block {
    width: 70%;
    margin: 30px;
  }
  .product-description {
    font-size: 1.3rem;
  }
  .get-started-footer {
    height: 17vh;
    margin-top: -7%;
  }

  .footer-links {
    padding-top: 7%;
    padding-left: 8%;
  }

  .footer-link {
    font-family: "Open sans", sans-serif !important;
    font-style: normal;
    font-weight: normal;
    font-size: 8px;
    line-height: 8px;
  }

  .footer-mail {
    font-family: "Open sans", sans-serif !important;
    color: #fff !important;
    text-decoration: underline;
    font-size: 0.8rem;
    padding-top: 0%;
    padding-left: 50%;
  }
}
